<template>
  <v-dialog v-model="dialog" persistent max-width="600px">
    <template v-slot:activator="{ on, attrs }">
      <!-- build button for add camping  -->
      <v-btn
        class="ma-5"
        outlined
        medium
        v-bind="attrs"
        v-on="on"
        color="primary"
      >
        <v-col cols="12"> <v-icon>mdi-plus</v-icon> add Camping </v-col>
      </v-btn>
      <!-- end  build button for add camping  -->
    </template>
    <v-form ref="form" lazy-validation v-model="valid">
      <v-card>
        <!-- title card dialog  -->
        <v-card-title>
          <v-row>
            <v-col>
              <span class="text-h5">Add Camping</span>
            </v-col>
            <v-col>
              <v-switch
                v-model="campingStatus"
                inset
                :label="`Camping is : ` + campingStateText"
              ></v-switch>
            </v-col>
          </v-row>
        </v-card-title>
        <!--  -->
        <v-card-text>
          <v-container>
            <v-row>
              <!-- title field card  -->
              <v-col cols="12">
                <v-text-field
                  :rules="nameRules"
                  label="Camping Name Ar *"
                  required
                  v-model="camping.title.ar"
                ></v-text-field>
              </v-col>
              <v-col cols="12">
                <v-text-field
                  :rules="nameRules"
                  label="Camping Name En *"
                  required
                  v-model="camping.title.en"
                ></v-text-field>
              </v-col>
              <v-col cols="12">
                <v-text-field
                  :rules="nameRules"
                  label="Camping Name Tr *"
                  required
                  v-model="camping.title.tr"
                ></v-text-field>
              </v-col>
              <!--  -->
              <!-- Description field card  -->
              <v-col cols="12">
                <v-textarea
                  label="Camping Description Ar*"
                  v-model="camping.description.ar"
                  :rules="descriptionRules"
                >
                </v-textarea>
              </v-col>
              <v-col cols="12">
                <v-textarea
                  label="Camping Description En *"
                  v-model="camping.description.en"
                  :rules="descriptionRules"
                >
                </v-textarea>
              </v-col>
              <v-col cols="12">
                <v-textarea
                  label="Camping Description Tr *"
                  v-model="camping.description.tr"
                  :rules="descriptionRules"
                >
                </v-textarea>
              </v-col>
              <!-- total amount  field card  -->
              <v-col cols="12">
                <v-text-field
                  prepend-inner-icon="mdi-currency-usd"
                  label="Total Amount *"
                  v-model="camping.total_amount"
                  :rules="totalAmountRules"
                >
                </v-text-field>
              </v-col>
              <!--  -->
              <!--  build  selection field from section-->
              <v-col cols="12">
                <v-autocomplete
                  v-model="camping.section_id"
                  label="sections "
                  :items="
                    allSections.map((item) => {
                      return {
                        name: item.name[lang],
                        id: item.id,
                      };
                    })
                  "
                  item-text="name"
                  item-value="id"
                >
                  <template v-slot:selection="data">
                    <v-chip
                      class="primary"
                      v-bind="data.attrs"
                      :input-value="data.title"
                      close
                      @click="data.select"
                      @click:close="removeSection(data.item)"
                    >
                      {{ data.item.name }}
                    </v-chip>
                  </template>
                </v-autocomplete>
              </v-col>
              <!-- end build  selection field from section-->

              <!--  build  selection field from project-->
              <v-col cols="12">
                <v-autocomplete
                  v-model="camping.project_id"
                  label="projects "
                  :items="
                    allProjects.map((item) => {
                      return {
                        title: item.title[lang],
                        id: item.id,
                      };
                    })
                  "
                  item-text="title"
                  item-value="id"
                >
                  <template v-slot:selection="data">
                    <v-chip
                      class="primary"
                      v-bind="data.attrs"
                      :input-value="data.title"
                      close
                      @click="data.select"
                      @click:close="remove(data.item)"
                    >
                      {{ data.item.title }}
                    </v-chip>
                  </template>
                </v-autocomplete>
              </v-col>
              <!-- end build  selection field from project-->

              <!--  build  selection field from nation-->
              <build-nation-field v-on:changNation="changNation" />
              <!-- end  build  selection field from nation-->
              <v-hover v-slot="{ hover }" open-delay="1000">
                <v-col cols="12">
                  <v-text-field
                    label="Camping Video"
                    required
                    v-model="camping.vedio_url"
                  ></v-text-field>

                  <v-container
                    v-html="camping.vedio_url"
                    v-if="camping.vedio_url && hover"
                    class="video"
                  >
                  </v-container>
                </v-col>
              </v-hover>

              <!-- build image field  -->
              <v-col cols="12">
                <v-file-input
                  :rules="imageRules"
                  v-model="file"
                  chips
                  accept="image/png, image/jpeg"
                  label="Image"
                  @change="onFileChange"
              /></v-col>
              <v-col cols="12">
                <v-img
                  contain
                  max-height="250"
                  aspect-ratio="1"
                  v-show="image"
                  :lazy-src="image"
                  :src="image"
                >
                </v-img>
              </v-col>
            </v-row>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primary" text @click="restData"> Cancel </v-btn>
          <v-btn
            color="secondary"
            class="add-office-button"
            text
            @click="addCamping"
          >
            <span v-if="!progressAddCamping">Add Camping</span>
            <v-progress-circular
              v-if="progressAddCamping"
              color="primary"
              indeterminate
            ></v-progress-circular>
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-form>
  </v-dialog>
</template>
<script>
import { campingController } from "./camping";
export default {
  mounted() {
    this.camping.status = false;
  },
  data() {
    return {
      campingStateText: "An Active",
      campingStatus: false,
      // form
      valid: true,
      // validation form
      nameRules: [(v) => !!v || "Name is required"],
      descriptionRules: [(v) => !!v || "Description is required"],
      imageRules: [(v) => !!v || "chose image ."],
      // image
      file: null,
      image: null,
      camping: {
        title: {},
        description: {},
      },
      areasRules: [(v) => !!(v && v.length) || "Choose at least office"],
      totalAmountRules: [
        (v) => !!v || "type the total amount ",
        (v) => this.isNumeric(v) || "type number valed ",
      ],
      //
      progressAddCamping: false,
      isUpdating: false,
      dialog: this.dialogController,
      allProjects: this.$store.getters.projects,
      allSections: this.$store.getters.sections,
      //  selectedAreas contains the id of areas from send this ids to server ..
    };
  },

  watch: {
    isUpdating(val) {
      if (val) {
        setTimeout(() => (this.isUpdating = false), 3000);
      }
    },
    campingStatus(state) {
      this.camping.status = state;
      if (state) {
        this.campingStateText = "Active";
      } else {
        this.campingStateText = "An Active";
      }
    },
  },

  methods: {
    isNumeric: function (n) {
      return !isNaN(parseFloat(n)) && isFinite(n);
    },
    remove() {
      this.camping.project_id = null;
    },
    removeSection() {
      this.camping.section_id = null;
    },
    changNation(nation) {
      this.camping.nation_id = nation;
    },
    addCamping: async function () {
      if (this.$refs.form.validate()) {
        this.progressAddCamping = true;
        var state = await campingController.addCamping(this.file, this.camping);
        if (state) {
          this.$emit("getVal");
          this.dialog = false;
          this.restData();
        } else {
          this.progressAddCamping = false;
        }
      }
    },
    onFileChange(e) {
      if (e != null) {
        const reader = new FileReader();
        reader.readAsDataURL(this.file);
        reader.onload = (e) => {
          this.image = e.target.result;
        };
      } else {
        this.file = null;
        this.image = null;
      }
    },

    restData() {
      this.dialog = false;
      this.image = null;
      this.file = null;
      this.camping = {
        title: {},
        description: {},
      };
      this.$refs.form.reset();
      this.progressAddCamping = false;
    },
  },
  computed: {
    lang() {
      return this.$store.getters.lang;
    },
  },
  components: {
    // "build-areas-field": () => import("./ArasField.vue"),
    "build-nation-field": () => import("../nation.vue"),
  },
};
</script>
 <style scoped>
.video {
  z-index: 1023;
  right: 20px;
  bottom: 10%;
  position: absolute;
}
</style>